import { InputBase } from './InputBase';

export class RangerSlider extends InputBase<string> {
  controlType = 'ranger-slider';
  id: number;
  min: number;
  max: number;
  step: number;
  miLabel: string;
  unit: string;
  constructor(options: {} = {}) {
    super(options);
    this.id = options['id'] || 1;
    this.min = options['min'] || 0;
    this.max = options['max'] || 10;
    this.step = options['step'] || 1;
    this.miLabel = options['miLabel'] || '';
    this.unit = options['unit'] || '';
  }
}
