import { InputBase } from './InputBase';

export class TypeAhead extends InputBase<string> {
  controlType = 'type-ahead';
  resultTransformer: Function;
  apiUpload: string;
  method: string;
  keyName: [];
  fieldName: Function;
  id: number;
  searchBy: string;

  constructor(options: {} = {}) {
    super(options);
    this.resultTransformer = options['resultTransformer'] || null;
    this.apiUpload = options['apiUpload'] || '';
    this.method = options['method'] || 'get';
    this.keyName = options['keyName'] || null;
    this.fieldName = options['fieldName'];
    this.id = options['id'] || 1;
    this.searchBy = options['searchBy'];
  }
}
