import { InputBase } from './InputBase';

export class UploadFileV2 extends InputBase<string> {
  controlType = 'uploadfilev2';
  id: number;
  accept: string;
  allowMaxSize: number;
  apiUpload: string;
  headers: object;
  uploadPath: string;
  multiple: boolean;
  paramName: string;
  resultTransformer: Function;
  title: string;
  dataFunction: Function;
  uploadFromDB: boolean;
  width: string;
  maxWidth: string;
  height: string;
  maxHeight: string;

  constructor(options: {} = {}) {
    super(options);
    this.id = options['id'] || 1;
    this.accept = options['accept'] || '';
    this.allowMaxSize = options['allowMaxSize'] || 2;
    this.apiUpload = options['apiUpload'] || '';
    this.headers = options['headers'] || {};
    this.uploadPath = options['uploadPath'] || '';
    this.multiple = options['multiple'] || false;
    this.paramName = options['paramName'] || 'files';
    this.resultTransformer = options['resultTransformer'];
    this.title = options['title'] || 'Upload';
    this.dataFunction = options['dataFunction'];
    this.uploadFromDB = options['uploadFromDB'] || false;
    this.width = options['width'];
    this.maxWidth = options['maxWidth'];
    this.height = options['height'];
    this.maxHeight = options['maxHeight'];
  }
}
