import { ValidatorFn, AsyncValidatorFn } from '@angular/forms';
export class InputBase<T> {
  label: string;
  key: string;
  value: T;
  classes: string[];
  group: number;
  group_classes: string[];
  validators: ValidatorFn[];
  asyncValidators: AsyncValidatorFn[];
  controlType: string;
  meta: any;
  disabled: boolean;
  step:  number;
  miLabel: string;
  unit: string;

  id?: number;
  searchBy?: string;
  onChange?: Function;

  accept?: string;
  allowMaxSize?: number;
  apiUpload?: string;
  uploadPath?: string;
  multiple?: boolean;
  resultKey?: string;
  keyName?: [];
  // custom-dropdown - dropdown - ListCheckBox - Radio
  options?: { label: string; value: string }[];

  // dropzone
  message?: string;
  upload_path?: string;
  acceptedFiles?: string;
  maxFilesize?: number;
  maxFiles?: number;
  showPreview?: boolean;
  url?: string;
  headers?: object;
  paramName?: string;
  resultTransformer?: Function;
  title: string;
  zoom: boolean;

  // text-box + tinymce
  min?: number;
  max?: number;
  type?: string;
  rows?: number;

  // mask-text
  mask?: {}[];
  valueWithCharacter?: boolean;
  guide?: boolean;
  placeholder?: string;

  // tinymce
  height?: string;
  plugins?: string;

  // datepicker
  showIcon?: Boolean;
  monthNavigator?: Boolean;
  yearNavigator?: Boolean;
  yearRange?: string;
  showTime?: Boolean;
  timeOnly?: Boolean;
  dateFormat?: string;
  minDate?: Date;
  maxDate?: Date;

  // select2
  tags?: Boolean;
  fieldName?: Function;
  lengthToSearch?: number;
  isSelectAll?: Boolean;
  selectBtnText?: string;
  clearBtnText?: string;
  dropdownParent?: any;
  method?: string;
  numberOfChar?: Number;
  dataFunction?: Function;
  uploadFromDB?: boolean;
  content?: Text;

  // uploadFile
  width: string;
  maxWidth: string;
  maxHeight: string;
  constructor(
    options: {
      label?: string;
      key?: string;
      value?: T;
      classes?: string[];
      group?: number;
      group_classes?: string[];
      validators?: ValidatorFn[];
      asyncValidators?: AsyncValidatorFn[];
      controlType?: string;
      meta?: any;
      disabled?: boolean;
    } = {}
  ) {
    this.label = options.label || '';
    this.key = options.key || '';
    this.value = options.value;
    this.classes = options.classes || [];
    this.group = options.group === undefined ? 1 : options.group;
    this.group_classes = options.group_classes || ['col-12'];
    this.validators = Array.isArray(options.validators) ? options.validators : [];
    this.asyncValidators = Array.isArray(options.asyncValidators) ? options.asyncValidators : [];
    this.controlType = options.controlType || '';
    this.meta = options.meta;
    this.disabled = options.disabled;
  }
}
