import { Component, OnInit, Input, forwardRef, ElementRef, ViewChild, Inject, Injector } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import * as _ from 'lodash';
import { DropzoneComponent, DropzoneDirective, DropzoneConfigInterface } from 'ngx-dropzone-wrapper';



@Component({
  // tslint:disable-next-line:component-selector
  selector: 'custom-dropzone',
  templateUrl: './dropzone.component.html',
  styleUrls: ['./dropzone.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CustomDropzoneComponent),
      multi: true
    }
  ]
})

export class CustomDropzoneComponent implements OnInit, ControlValueAccessor {
  @Input()
  message;
  @Input()
  upload_path;
  @Input()
  acceptedFiles;
  @Input()
  maxFilesize;
  @Input()
  maxFiles;
  @Input()
  showPreview;
  @Input()
  oldValue;
  @Input()
  url;
  @Input()
  headers;
  @Input()
  paramName;
  @Input()
  _value;
  @Input()
  resultTransformer;

  fileList = [];

  config: any = {
    addRemoveLinks: true
  };
  private disabled: boolean;
  private onChange: Function;
  private onTouched: Function;

  @ViewChild(DropzoneComponent)
  componentRef?: DropzoneComponent;
  @ViewChild(DropzoneDirective)
  directiveRef?: DropzoneDirective;
  constructor(private element: ElementRef, private injector: Injector) {
    this.onChange = (val: any) => {};
    this.onTouched = () => {};
    this.disabled = false;
  }
  writeValue(val: any): void {
    if (val) {
      this.value = val;
    }
  }

  get value() {
    return this._value;
  }

  set value(val) {
    this._value = val;
    this.onChange(this._value);
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  ngOnInit() {
    const UPLOAD_DIRECTIVE_HEADERS = this.injector.get('UPLOAD_DIRECTIVE_HEADERS');
    const UPLOAD_DIRECTIVE_API_URL = this.injector.get('UPLOAD_DIRECTIVE_API_URL');
    const $this = this;
    if (this.maxFiles !== 1 && !_.isEmpty($this.oldValue)) {
      _.forEach(this.oldValue, function(value) {
        $this.fileList.push(value);
        $this.value = $this.fileList;
      });
    }
    if (this.maxFiles === 1 && !_.isEmpty($this.oldValue)) {
      $this.value = $this.oldValue;
    }
    this.config.params = {
      upload_path: this.upload_path
    };
    this.config.url = UPLOAD_DIRECTIVE_API_URL + this.url;
    this.config.headers = UPLOAD_DIRECTIVE_HEADERS;
    this.config.acceptedFiles = this.acceptedFiles;
    this.config.maxFilesize = this.maxFilesize;
    this.config.maxFiles = this.maxFiles;
    this.config.paramName = this.paramName;
  }

  onUploadError($event) {
    // AppInjector.get(NotificationService).show('warning', $event[1], 5000);
    console.log('warning', $event);
  }

  onUploadSuccess($event) {
    if (this.maxFiles !== 1) {
      this.fileList.push(this.resultTransformer($event[1]));
      this.value = this.fileList;
    } else {
      this.value = this.resultTransformer($event[1]);
    }
  }

  onReset() {
    if (this.componentRef && this.componentRef.directiveRef) {
      this.componentRef.directiveRef.reset();
      this.fileList = [];
      this.value = this.fileList;
    }
  }

  onFileRemoved($event) {

    const obj = JSON.parse($event.xhr.response);
    if (this.maxFiles !== 1) {
      this.fileList.splice(this.fileList.indexOf(this.resultTransformer(obj), 1));
      this.value = this.fileList;
    } else {
      this.value = null;
    }
  }

  deleteItem(item) {
    this.fileList.splice(this.fileList.indexOf(item), 1);
    this.value = this.fileList;
  }

  // https://github.com/zefoy/ngx-dropzone-wrapper
}
