import { Injectable } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { InputBase } from '../models/InputBase';
import * as _ from 'lodash';

@Injectable()
export class FormControlService {
  constructor() {}

  toFormGroup(inputs: InputBase<any>[]) {
    // tslint:disable-next-line:prefer-const
    let group: any = {};
    inputs.forEach(input => {
      // tslint:disable-next-line:prefer-const
      const arrayValidator = [];
      _.forEach(input.validators, function(value) {
        arrayValidator.push((value as any).validator);
      });
      group[input.key] = new FormControl(input.value || '', arrayValidator, input.asyncValidators);
    });
    return new FormGroup(group);
  }
}
