import { Component, OnInit, Input, forwardRef, ElementRef, AfterViewInit } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import * as _ from 'lodash';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'custom-checkbox',
  templateUrl: './custom-checkbox.component.html',
  styleUrls: ['./custom-checkbox.component.css'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CustomCheckboxComponent),
      multi: true
    }
  ]
})
export class CustomCheckboxComponent implements OnInit, ControlValueAccessor {
  @Input()
  _value;
  private disabled: boolean;
  private onChang: Function;
  private onTouched: Function;

  @Input() onChange;

  constructor(private element: ElementRef) {
    this.onChang = (val: any) => {};
    this.onTouched = () => {};
    this.disabled = false;
  }
  writeValue(val: any): void {
    if (val) {
      this.value = val;
    }
  }

  get value() {
    return this._value;
  }

  set value(val) {
    this._value = val;
    this.onChang(this._value);
  }
  registerOnChange(fn: any): void {
    this.onChang = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  ngOnInit() {
    this.value = false;
  }

  onChangeFunction() {
    this.onChange();
  }
}
