import { Component, Input, forwardRef, Inject, AfterViewInit, OnChanges, Injector } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import * as _ from 'lodash';
import { SelectionService } from '../selection-by-api/selection-by-api';
declare const $: any;

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'multi-selection-by-api',
  templateUrl: './multi-selection-by-api.component.html',
  styleUrls: ['./multi-selection-by-api.component.css'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => MultiSelectionByApiComponent),
      multi: true
    },
    SelectionService
  ]
})
export class MultiSelectionByApiComponent implements ControlValueAccessor, OnChanges, AfterViewInit {
  @Input() _value;
  @Input() resultTransformer;
  @Input() apiUpload;
  @Input() headers;
  @Input() fieldName;
  @Input() lengthToSearch;
  @Input() isClear: Boolean;
  @Input() method;
  @Input() id;
  public text;
  public options;
  public isShow = false;
  public result = [];
  public KEY_CODE = {
    UP_ARROW: 38,
    DOWN_ARROW: 40
  };
  public url = (apiUrl, search) => {
    return apiUrl + this.apiUpload + search;
  }
  constructor(private api: SelectionService, private injector: Injector) {}
  propagateChange = (val: any) => {};

  ngAfterViewInit() {
    setTimeout(() => {
      if (this.value) {
        this.result = this.value;
      }
    }, 1);
  }

  ngOnChanges() {
    if (this.isClear === true) {
      setTimeout(() => {
        this.value = [];
        this.result = [];
        _.forEach(this.options, function(value) {
          value.selected = false;
        });
        this.mapOptionsResult(this.options, this.result);
      }, 1);
    }
  }

  filterData(event) {
    const UPLOAD_DIRECTIVE_HEADERS = this.injector.get('UPLOAD_DIRECTIVE_HEADERS');
    const UPLOAD_DIRECTIVE_API_URL = this.injector.get('UPLOAD_DIRECTIVE_API_URL');
    if (event.length >= this.lengthToSearch) {
      const api = this.url(UPLOAD_DIRECTIVE_API_URL, event);
      const requestHeaders = UPLOAD_DIRECTIVE_HEADERS;
      this.api.getAll(api, requestHeaders, this.method).subscribe(response => {
        this.options = this.resultTransformer(response);
        this.mapOptionsResult(this.options, this.result);
      });
      this.isShow = true;
    }
  }

  detailClick(item) {
    const $this = this;
    if (item.selected) {
      _.remove(this.result, function(n) {
        return JSON.stringify(n) === JSON.stringify(item);
      });
      _.forEach(this.options, function(value) {
        if (JSON.stringify(value) === JSON.stringify(item)) {
          value.selected = false;
        }
      });
      this.mapOptionsResult(this.options, this.result);
      this.isShow = false;
      this.value = this.result;
    } else {
      this.result.push(item);
      this.mapOptionsResult(this.options, this.result);
      this.isShow = false;
      this.value = this.result;
    }
  }

  changeStatusOptions() {
    this.isShow === false ? (this.isShow = true) : (this.isShow = false);
  }

  _isEmpty(array) {
    return _.isEmpty(array) ? true : false;
  }

  removeItem(item) {
    const $this = this;
    _.remove(this.result, function(n) {
      return JSON.stringify(n) === JSON.stringify(item);
    });
    _.forEach(this.options, function(value) {
      if (JSON.stringify(value) === JSON.stringify(item)) {
        value.selected = false;
      }
    });
    this.mapOptionsResult(this.options, this.result);
    this.isShow = false;
    this.value = this.result;
  }

  mapOptionsResult(options, result) {
    _.map(options, item => {
      _.forEach(result, function(value) {
        if (JSON.stringify(item) === JSON.stringify(value)) {
          item.selected = true;
        }
      });
    });
    this.options = options;
  }

  get value() {
    return this._value;
  }

  set value(val) {
    this._value = val;
    this.propagateChange(this._value);
  }

  writeValue(value: any) {
    if (value !== undefined) {
      this.value = value;
    }
  }

  registerOnChange(fn) {
    this.propagateChange = fn;
  }

  registerOnTouched() {}
}
