import { Component, OnInit, Injector, forwardRef, Input, AfterViewInit, OnChanges, ViewChild } from '@angular/core';
import { Observable, Subject, merge } from 'rxjs';
import { debounceTime, map, distinctUntilChanged, filter } from 'rxjs/operators';
import { SelectionService } from '../selection-by-api/selection-by-api';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';
import { NgbTypeahead } from '@ng-bootstrap/ng-bootstrap';
@Component({
  // tslint:disable-next-line:component-selector
  selector: 'typeahead',
  templateUrl: './typeahead.component.html',
  styleUrls: ['./typeahead.component.css'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TypeaheadComponent),
      multi: true
    },
    SelectionService
  ]
})
export class TypeaheadComponent implements ControlValueAccessor, OnInit, AfterViewInit, OnChanges {
  @Input() _value;
  @Input() id;
  @Input() resultTransformer;
  @Input() apiUpload;
  @Input() keyName;
  @Input() method;
  @Input() fieldName;
  @Input() searchBy;
  @Input()
  isClear: Boolean;
  @ViewChild('instance', {}) instance: NgbTypeahead;
  focus$ = new Subject<string>();
  click$ = new Subject<string>();
  public statesWithFlags: any[] = [];
  public model: any;
  public UPLOAD_DIRECTIVE_HEADERS = this.injector.get('UPLOAD_DIRECTIVE_HEADERS');
  public UPLOAD_DIRECTIVE_API_URL = this.injector.get('UPLOAD_DIRECTIVE_API_URL');
  public url = (apiUrl, search) => {
    return apiUrl + this.apiUpload + search;
    // tslint:disable-next-line:semicolon
  };
  // search = (text$: Observable<string>) =>
  //   text$.pipe(
  //     debounceTime(200),
  //     map(term =>
  //       term === '' ? [] : this.statesWithFlags.filter(v => v[this.searchBy].toLowerCase().indexOf(term.toLowerCase()) > -1).slice(0, 10)
  //     )
  //     // tslint:disable-next-line:semicolon
  //   );
  search = (text$: Observable<string>) => {
    const debouncedText$ = text$.pipe(
      debounceTime(200),
      distinctUntilChanged()
    );
    const clicksWithClosedPopup$ = this.click$.pipe(filter(() => !this.instance.isPopupOpen()));
    const inputFocus$ = this.focus$;

    return merge(debouncedText$, inputFocus$, clicksWithClosedPopup$).pipe(
      // tslint:disable-next-line:max-line-length
      // map(term =>
      //   term === '' ? [] : this.statesWithFlags.filter(v => v[this.searchBy].toLowerCase().indexOf(term.toLowerCase()) > -1).slice(0, 10)
      // )
      map(term =>
        // tslint:disable-next-line:max-line-length
        term === ''
          ? this.statesWithFlags
          : this.statesWithFlags.filter(v => v[this.searchBy].toLowerCase().indexOf(term.toLowerCase()) > -1).slice(0, 10)
      )
    );
    // tslint:disable-next-line:semicolon
  };

  constructor(private api: SelectionService, private injector: Injector) {}
  propagateChange = (val: any) => {};

  ngOnInit() {}

  ngOnChanges() {
    setTimeout(() => {
      if (this.isClear) {
        this.value = null;
        this.model = this.value;
      }
    }, 1);
  }

  ngAfterViewInit() {
    setTimeout(() => {
      const apiT = this.url(this.UPLOAD_DIRECTIVE_API_URL, '');
      const requestHeaders = this.UPLOAD_DIRECTIVE_HEADERS;
      this.api.getAll(apiT, requestHeaders, this.method).subscribe(response => {
        this.statesWithFlags = this.resultTransformer(response);
      });
      if (this.value) {
        this.model = this.value;
      }
    }, 1);
  }

  filterData(val) {
    if (typeof val !== 'object') {
      const api = this.url(this.UPLOAD_DIRECTIVE_API_URL, val);
      const requestHeaders = this.UPLOAD_DIRECTIVE_HEADERS;
      this.api.getAll(api, requestHeaders, this.method).subscribe(response => {
        this.statesWithFlags = this.resultTransformer(response);
      });
    } else {
      this.value = val;
    }
  }

  get value() {
    return this._value;
  }

  set value(val) {
    this._value = val;
    this.propagateChange(this._value);
  }

  writeValue(value: any) {
    if (value !== undefined) {
      this.value = value;
    }
  }

  registerOnChange(fn) {
    this.propagateChange = fn;
  }

  registerOnTouched() {}
}

