import { HttpClient, HttpParams } from '@angular/common/http';

import { Observable } from 'rxjs';

import { Http, Headers, Response, RequestOptions } from '@angular/http';
import { map } from 'rxjs/operators';
import { Injectable } from '@angular/core';

@Injectable()
export class FileService {
  private url = '/api/v1/upload';
  http: HttpClient;

  constructor(http: HttpClient) {
    this.http = http;
  }

  upload(params, apiUpload, headers): Observable<any> {
    // tslint:disable-next-line:prefer-const
    let formData: FormData = new FormData();
    // tslint:disable-next-line:forin
    for (const key in params) {
      formData.append(key, params[key]);
    }
    return this.http.post(apiUpload, formData, { headers: headers }).pipe(map((response: Response) => response));
  }

  delete(params, apiUpload): Observable<any> {
    // tslint:disable-next-line:prefer-const
    let formData: FormData = new FormData();
    // tslint:disable-next-line:forin
    for (const key in params) {
      formData.append(key, params[key]);
    }

    return this.http.post(apiUpload, formData).pipe(map((response: Response) => response));
  }
}
