import { Component, OnInit, forwardRef, Input, AfterViewInit, ViewChild, OnChanges } from '@angular/core';
import { SelectionService } from '../selection-by-api/selection-by-api';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';
import { NgbTypeahead } from '@ng-bootstrap/ng-bootstrap';
import { Subject, Observable, merge } from 'rxjs';
import { debounceTime, distinctUntilChanged, filter, map } from 'rxjs/operators';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'typeahead-without-api',
  templateUrl: './typeahead.component.html',
  styleUrls: ['./typeahead.component.css'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TypeaheadWithoutApiComponent),
      multi: true
    },
    SelectionService
  ]
})
export class TypeaheadWithoutApiComponent implements ControlValueAccessor, OnInit, AfterViewInit, OnChanges {
  @Input() _value;
  @Input() id;
  @Input() options;
  @Input()
  isClear: Boolean;

  states: any = [];

  model: any;

  @ViewChild('instance', {}) instance: NgbTypeahead;
  focus$ = new Subject<string>();
  click$ = new Subject<string>();
  formatter = x => x.label;

  search = (text$: Observable<string>) => {
    const debouncedText$ = text$.pipe(
      debounceTime(200),
      distinctUntilChanged()
    );
    const clicksWithClosedPopup$ = this.click$.pipe(filter(() => !this.instance.isPopupOpen()));
    const inputFocus$ = this.focus$;

    return merge(debouncedText$, inputFocus$, clicksWithClosedPopup$).pipe(
      // tslint:disable-next-line:max-line-length
      map(term =>
        (term === '' ? this.states : this.states.filter(v => v.label.toLowerCase().indexOf(term.toLowerCase()) > -1)).slice(0, 10)
      )
    );
    // tslint:disable-next-line:semicolon
  };

  constructor() {}
  propagateChange = (val: any) => {};

  ngOnInit() {}

  ngOnChanges() {
    setTimeout(() => {
      if (this.isClear) {
        this.value = null;
        this.model = this.value;
      }
    }, 1);
  }


  filterData(data) {
    if (typeof data === 'object') {
      this.value = data;
    }
  }

  ngAfterViewInit() {
    setTimeout(() => {
      this.states = this.options;
      if (this.value) {
        this.model = this.value;
      }
    }, 1);
  }

  get value() {
    return this._value;
  }

  set value(val) {
    this._value = val;
    this.propagateChange(this._value);
  }

  writeValue(value: any) {
    if (value !== undefined) {
      this.value = value;
    }
  }

  registerOnChange(fn) {
    this.propagateChange = fn;
  }

  registerOnTouched() {}
}

