import { Component, OnInit, ViewChild, forwardRef, Input, AfterViewInit } from '@angular/core';
// import { IBlock, BlockEditorProviderComponent } from '@vicoders/vc-gutenberg';
// import { serialize, parse } from '@wordpress/blocks';
import { NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'gutenberg',
  templateUrl: './gutenburg.component.html',
  styleUrls: ['./gutenburg.component.css'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => GutenburgComponent),
      multi: true
    }
  ]
})
export class GutenburgComponent implements OnInit, AfterViewInit {
  // blocks: IBlock[];
  // @ViewChild('editor')
  // protected editor: BlockEditorProviderComponent;
  @Input() _value;
  @Input() oldValue;
  propagateChange = (val: any) => {};
  constructor() {
    // this.blocks = [];
  }

  ngAfterViewInit() {
    setTimeout(() => {
      if (this.oldValue) {
        // this.blocks = parse(this.oldValue);
        this.value = this.oldValue;
      }
    }, 1000);
  }

  // onBlocksChanged(item: IBlock[]) {
  //   const html = serialize(item);
  //   this.value = html;
  //   console.log('value', this.value);
  // }
  get value() {
    return this._value;
  }

  set value(val) {
    this._value = val;
    this.propagateChange(this._value);
  }

  writeValue(value: any) {
    if (value !== undefined) {
      this.value = value;
    }
  }

  registerOnChange(fn) {
    this.propagateChange = fn;
  }

  registerOnTouched() {}
  ngOnInit() {}
}
