import { Component, OnInit, Input, forwardRef, ElementRef, ChangeDetectorRef, OnChanges } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import * as _ from 'lodash';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'custom-date-time-picker',
  templateUrl: './custom-date-time-picker.component.html',
  styleUrls: ['./custom-date-time-picker.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CustomDateTimePickerComponent),
      multi: true
    }
  ]
})
export class CustomDateTimePickerComponent implements ControlValueAccessor, OnInit, OnChanges {
  @Input() _value: Date;

  @Input() showIcon: Boolean;

  @Input() monthNavigator: Boolean;
  @Input() yearNavigator: Boolean;
  @Input() yearRange: string;
  @Input() showTime: Boolean;
  @Input() timeOnly: Boolean;
  @Input() dateFormat: string;
  @Input() isDisabled: Boolean;
  @Input() minDate: Date;
  @Input() maxDate: Date;
  @Input() isClear: Boolean;

  dates: Date[];

  rangeDates: Date[];

  // minDate: Date;

  // maxDate: Date;

  en: any;

  tr: any;

  invalidDates: Array<Date>;

  private disabled: boolean;
  private onChange: Function;
  private onTouched: Function;

  constructor(private cdRef: ChangeDetectorRef, private element: ElementRef) {
    this.onChange = (val: any) => {};
    this.onTouched = () => {};
    this.disabled = false;
  }

  writeValue(val: any): void {
    if (val) {
      this.value = val;
    }
  }

  get value() {
    return this._value;
  }

  set value(val) {
    this._value = val;
    this.onChange(this._value);
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  ngOnChanges() {
    if (this.isClear === true) {
      setTimeout(() => {
        this.value = null;
      }, 1);
    }
  }

  ngOnInit() {
    this.en = {
      firstDayOfWeek: 0,
      dayNames: ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'],
      dayNamesShort: ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'],
      dayNamesMin: ['Su', 'Mo', 'Tu', 'We', 'Th', 'Fr', 'Sa'],
      // tslint:disable-next-line:max-line-length
      monthNames: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
      monthNamesShort: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
      today: 'Today',
      clear: 'Clear'
    };

    this.tr = {
      firstDayOfWeek: 1
    };

    const today = new Date();
    const month = today.getMonth();
    const year = today.getFullYear();
    const prevMonth = month === 0 ? 11 : month - 1;
    const prevYear = prevMonth === 11 ? year - 1 : year;
    const nextMonth = month === 11 ? 0 : month + 1;
    const nextYear = nextMonth === 0 ? year + 1 : year;
    // this.minDate = new Date('11/05/2018');
    // this.minDate.setMonth(prevMonth);
    // this.minDate.setFullYear(prevYear);
    // this.maxDate = new Date('11/10/2018');
    // this.maxDate.setMonth(nextMonth);
    // this.maxDate.setFullYear(nextYear);

    const invalidDate = new Date();
    invalidDate.setDate(today.getDate() - 1);
    this.invalidDates = [today, invalidDate];
  }

  change(value) {
    this.value = value;
  }
}

