import { InputBase } from './InputBase';

export class Select2 extends InputBase<string> {
  controlType = 'select2';
  options: { label: string; value: string }[] = [];
  tags: Boolean;
  placeholder: string;
  id: number;
  isSelectAll: Boolean;
  selectBtnText: string;
  clearBtnText: string;
  dropdownParent: any;

  constructor(options: {} = {}) {
    super(options);
    this.id = options['id'] || 1;
    this.options = options['options'] || [];
    this.tags = options['tags'] || false;
    this.placeholder = options['placeholder'] || '';
    this.isSelectAll = options['isSelectAll'] || false;
    this.selectBtnText = options['selectBtnText'] || 'Select All';
    this.dropdownParent = options['dropdownParent'];
    this.clearBtnText = options['clearBtnText'] || 'Clear All';
  }
}
