import { InputBase } from './InputBase';

export class SelectionByApi extends InputBase<string> {
  controlType = 'selection-by-api';
  resultTransformer: Function;
  headers: object;
  apiUpload: string;
  fieldName: Function;
  multiple: boolean;
  lengthToSearch: number;
  method: string;

  constructor(options: {} = {}) {
    super(options);
    this.resultTransformer = options['resultTransformer'] || null;
    this.apiUpload = options['apiUpload'] || '';
    this.headers = options['headers'] || null;
    this.fieldName = options['fieldName'] || null;
    this.multiple = options['multiple'] || false;
    this.lengthToSearch = options['lengthToSearch'] || 3;
    this.method = options['method'] || 'get';
    this.id = options['id'] || 1;
  }
}
