import { InputBase } from './InputBase';

export class SwitchInput extends InputBase<string> {
  controlType = 'switch-input';
  id: number;
  isDisabled: Boolean;

  constructor(options: {} = {}) {
    super(options);
    this.id = options['id'] || 1;
    this.isDisabled = options['isDisabled'] || false;
  }
}
