import { InputBase } from './InputBase';

export class DateTimePicker extends InputBase<string> {
  controlType = 'datetimepicker';
  showIcon: Boolean;
  monthNavigator: Boolean;
  yearNavigator: Boolean;
  yearRange: string;
  showTime: Boolean;
  timeOnly: Boolean;
  dateFormat: string;
  minDate: Date;
  maxDate: Date;

  constructor(options: {} = {}) {
    super(options);
    this.showIcon = options['showIcon'] || false;
    this.monthNavigator = options['monthNavigator'] || false;
    this.yearNavigator = options['yearNavigator'] || false;
    this.yearRange = options['yearRange'] || '1995:2050';
    this.showTime = options['showTime'] || false;
    this.timeOnly = options['timeOnly'] || false;
    this.dateFormat = options['dateFormat'] || 'dd/mm/yy';
    this.minDate = options['minDate'] || null;
    this.maxDate = options['maxDate'] || null;
  }
}
