import { Component, Input, forwardRef, ElementRef, AfterViewInit, OnChanges } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import * as _ from 'lodash';
declare const $: any;

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'single-select2',
  templateUrl: './single-select2.component.html',
  styleUrls: ['./single-select2.component.css'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => SingleSelect2Component),
      multi: true
    }
  ]
})
export class SingleSelect2Component implements AfterViewInit, ControlValueAccessor, OnChanges {
  @Input()
  placeholder: string;
  @Input()
  options: any;
  @Input()
  id: number;
  @Input()
  _value: any;
  @Input()
  isClear: Boolean;
  @Input() isDisabled: Boolean;
  @Input()
  dropdownParent: any;
  defaultValue = null;

  private disabled: boolean;
  private onChange: Function;
  private onTouched: Function;

  constructor(private element: ElementRef) {
    this.onChange = (val: any) => {};
    this.onTouched = () => {};
    this.disabled = false;
  }
  writeValue(val: any): void {
    if (val) {
      this.value = val;
    }
  }

  get value() {
    return this._value;
  }

  set value(val) {
    this._value = val;
    this.onChange(this._value);
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  ngAfterViewInit() {
    this.defaultValue = this.value;
    setTimeout(() => {
      this.actionRender();
    }, 1);
  }

  ngOnChanges() {
    if (this.isClear === true) {
      this.defaultValue = undefined;
      setTimeout(() => {
        $('#single-select2-reactive-form-' + this.id)
          .select2()
          .select2('val', 'ok');
      }, 1);
    }
  }
  formatValues(data) {
    return data;
  }

  actionRender() {
    // tslint:disable-next-line:prefer-const
    let $this = this;

    // tslint:disable-next-line:prefer-const
    let options: any = {
      formatResult: this.formatValues,
      formatSelection: this.formatValues
    };

    if (this.placeholder !== undefined && this.placeholder != null) {
      options.placeholder = this.placeholder;
    }
    if (this.isDisabled !== undefined && this.isDisabled != null && this.isDisabled === true) {
      options.disabled = true;
    }
    if (!_.isNil(this.dropdownParent) && this.dropdownParent !== '') {
      options.dropdownParent = $(this.dropdownParent);
    }
    $('#single-select2-reactive-form-' + this.id).select2(options);
    if (_.isUndefined(this.defaultValue)) {
      const a = $('#single-select2-reactive-form-' + this.id)
        .select2()
        .select2('val');
      this.value = _.find($this.options, function(o) {
        return o.value === +a;
      });
    }
    if (!_.isUndefined(this.defaultValue)) {
      $('#single-select2-reactive-form-' + this.id)
        .select2()
        .val(this.value.value)
        .trigger('change');
    }
    $('#single-select2-reactive-form-' + this.id).change(function() {
      const value = $(this).val();
      $this.value = _.find($this.options, function(o) {
        return o.value === value;
      });
    });
  }
}
