import { InputBase } from './InputBase';

export class Captcha extends InputBase<string> {
  controlType = 'captcha';
  numberOfChar: Number;
  message: string;

  constructor(options: {} = {}) {
    super(options);
    this.numberOfChar = options['numberOfChar'] || 4;
    this.type = options['type'] || null;
    this.message = options['message'] || 'Captcha not found';
  }
}
