import { Component, OnInit, Input, forwardRef } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { Provinces } from './province';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'province-city',
  templateUrl: './province-city.component.html',
  styleUrls: ['./province-city.component.css'],
  providers: [{
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => ProvinceCityComponent),
    multi: true
  }]
})
export class ProvinceCityComponent implements ControlValueAccessor, OnInit {
  public provinces = Provinces;
  @Input() _value;
  constructor() {}

  propagateChange = (val: any) => {};

  ngOnInit() {
  }

  get value() {
    return this._value;
  }

  set value(val) {
    this._value = val;
    this.propagateChange(this._value);
  }

  selectedProvince(value) {
    this.value = value;
  }

  writeValue(value: any) {
    if (value !== undefined) {
      this.value = value;
    }
  }

  registerOnChange(fn) {
    this.propagateChange = fn;
  }

  registerOnTouched() {}
}
