import { InputBase } from './InputBase';

export class TinyMce extends InputBase<string> {
  controlType = 'tinymce';
  type: string;
  height: string;
  plugins: string;
  apiUpload: string;
  paramName: string;
  resultTransformer: Function;

  constructor(options: {} = {}) {
    super(options);
    this.type = options['type'] || '';
    this.height = options['height'] || '300';
    this.plugins = options['plugins'] || '';
    this.apiUpload = options['apiUpload'] || '';
    this.paramName = options['paramName'] || 'files';
    this.resultTransformer = options['resultTransformer'];
  }
}
