import { InputBase } from './InputBase';

export class UploadFileV3 extends InputBase<string> {
  controlType = 'uploadfilev3';
  id: number;
  accept: string;
  allowMaxSize: number;
  apiUpload: string;
  headers: object;
  uploadPath: string;
  multiple: boolean;
  paramName: string;
  resultTransformer: Function;
  resultKey: string;

  constructor(options: {} = {}) {
    super(options);
    this.id = options['id'] || 1;
    this.accept = options['accept'] || '';
    this.allowMaxSize = options['allowMaxSize'] || 2;
    this.apiUpload = options['apiUpload'] || '';
    this.headers = options['headers'] || {};
    this.uploadPath = options['uploadPath'] || '';
    this.multiple = options['multiple'] || false;
    this.paramName = options['paramName'] || 'files';
    this.resultTransformer = options['resultTransformer'];
    this.resultKey = options['resultKey'];
  }
}
