export const VALIDATOR_REQUIRED = 'required';
export const VALIDATOR_PATTERN = 'pattern';
export const VALIDATOR_MINLENGTH = 'minlength';
export const VALIDATOR_MAXLENGTH = 'maxlength';

import * as _ from 'lodash';

export const UpdateFormValue = (inputs, data, options?: {}) => {
  // tslint:disable-next-line:max-line-length
  const ableSimpleValue = [
    'textbox',
    'dropdown',
    'radio',
    'checkbox',
    'datetimepicker',
    'uploadfile',
    'phonecode',
    'textarea',
    'textbox-mask',
    'tinymce',
    'single-select2',
    'selection-by-api',
    'switch-input',
    'uploadfilev2',
    'uploadfilev3',
    'type-ahead',
    'type-ahead-without-api',
    'custom_dropdown',
    'ranger-slider',
    'gutenberg',
    'dropzone'
  ];
  _.forEach(data, function(value, key) {
    inputs = _.map(inputs, input => {
      if (input.key === key) {
        if (_.includes(ableSimpleValue, input.controlType)) {
          input.value = value;
          return input;
        }
        switch (input.controlType) {
          case 'list-checkbox':
            input.options = input.options.map(item => {
              for (const i of value) {
                if (item.value === i) {
                  item.checked = true;
                }
              }
              return item;
            });
            return input;
            break;
          case 'select2':
            input.options = input.options.map(item => {
              for (const i of value) {
                if (item.value === i) {
                  item.selected = true;
                }
              }
              return item;
            });
            return input;
            break;
        }
      }
      return input;
    });
  });
  return inputs;
};

export const UpdateInputsValue = (inputs, data) => {
  const ableType = ['dropdown', 'radio', 'list-checkbox', 'select2', 'single-select2', 'type-ahead-without-api', 'custom_dropdown'];
  _.forEach(data, function(value, key) {
    _.forEach(inputs, function(input) {
      if (input.key === key) {
        if (_.includes(ableType, input.controlType)) {
          input.options = value;
        }
        if (input.controlType === 'dropdown') {
          input.value = _.head(value);
        }
        if (input.controlType === 'radio') {
          input.value = (_.head(value) as any).value;
        }
      }
    });
  });
  return inputs;
};

