import { Component, Input, forwardRef, ElementRef, AfterViewInit, OnChanges } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import * as _ from 'lodash';
declare const $: any;

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'app-custom-select2',
  templateUrl: './custom-select2.component.html',
  styleUrls: ['./custom-select2.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CustomSelect2Component),
      multi: true
    }
  ]
})
export class CustomSelect2Component implements AfterViewInit, ControlValueAccessor, OnChanges {
  @Input()
  tags: Boolean;
  @Input()
  placeholder: string;
  @Input()
  options: any;
  @Input()
  id: number;
  @Input()
  _value: any[];
  @Input()
  isClear: Boolean;
  @Input()
  isSelectAll: Boolean;
  @Input()
  selectBtnText: string;
  @Input()
  clearBtnText: string;
  @Input() isDisabled: Boolean;
  @Input()
  dropdownParent: any;
  defaultValue = [];

  private disabled: boolean;
  private onChange: Function;
  private onTouched: Function;

  constructor(private element: ElementRef,) {
    this.onChange = (val: any) => {};
    this.onTouched = () => {};
    this.disabled = false;
  }
  writeValue(val: any): void {
    if (val) {
      this.value = val;
    }
  }

  get value() {
    return this._value;
  }

  set value(val) {
    this._value = val;
    this.onChange(this._value);
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  ngAfterViewInit() {
    setTimeout(() => {
      this.actionRender();
    }, 1000);
  }

  ngOnChanges() {
    if (this.isClear === true) {
      if (!_.isUndefined(this.options) && !_.isEmpty(this.options)) {
        _.map(this.options, i => {
          i.selected = undefined;
        });
        this.defaultValue = [];
        setTimeout(() => {
          this.actionRender();
        }, 1000);
      }
    }
  }

  actionRender() {
    // tslint:disable-next-line:prefer-const
    let $this = this;

    // tslint:disable-next-line:prefer-const
    let options: any = {};
    if (this.tags !== undefined && this.tags != null) {
      options.tags = this.tags;
    }
    if (this.isDisabled !== undefined && this.isDisabled != null && this.isDisabled === true) {
      options.disabled = true;
    }
    if (this.placeholder !== undefined && this.placeholder != null) {
      options.placeholder = this.placeholder;
    }
    if (!_.isNil(this.dropdownParent) && this.dropdownParent !== '') {
      options.dropdownParent = $(this.dropdownParent);
    }
    $('#select2-reactive-form-' + this.id).select2(options);

    // tslint:disable-next-line:prefer-const
    let oldData = $('#select2-reactive-form-' + this.id).select2('data');
    if (oldData != null && oldData !== undefined && !_.isEmpty(oldData)) {
      _.forEach(oldData, function(value) {
        $this.defaultValue.push(value.id);
      });
      this.value = this.defaultValue;
    }

    $('#select2-reactive-form-' + this.id).change(function() {
      // tslint:disable-next-line:prefer-const
      let value = $(this).val();
      $this.value = value;
    });
  }

  selectedAll() {
    _.map(this.options, i => {
      i.selected = true;
    });
    setTimeout(() => {
      this.defaultValue = [];
      this.actionRender();
    }, 1);
  }

  clearAll() {
    _.map(this.options, i => {
      delete i['selected'];
    });
    setTimeout(() => {
      this.defaultValue = [];
      this.value = this.defaultValue;
      this.actionRender();
    }, 1);
  }
}
