import { HttpClient } from '@angular/common/http';
import { catchError, map, tap } from 'rxjs/operators';
import * as _ from 'lodash';
import { AbstractControl, ValidationErrors, AsyncValidatorFn } from '@angular/forms';
import { Injectable, Injector } from '@angular/core';
import { AppInjector } from '../app-injector';
import { Observable } from 'rxjs';

@Injectable()
class ValidatorService {
  http = AppInjector.get(HttpClient);
  constructor() {}

  apiService(api, headers): Observable<any> {
    return this.http.get(api, { headers: headers }).pipe(
      tap(result => {}),
      catchError(error => {
        throw error;
      })
    );
  }
}

@Injectable({
  providedIn: 'root'
})
class Validators {
  constructor() {}

  isEqualValidator(matchValue: any) {
    return (control: AbstractControl): ValidationErrors | null => {
      const value = control.value;
      if (value) {
        const compareValue = control.root.get(matchValue).value;
        if (value !== compareValue) {
          return { notEqual: { valid: false, value: control.value } };
        }
      }
      return null;
    };
  }

  isDifferentValidator(matchValue: any) {
    return (control: AbstractControl): ValidationErrors | null => {
      const value = control.value;
      if (value) {
        const compareValue = control.root.get(matchValue).value;
        if (value === compareValue) {
          return { notDifferent: { valid: false, value: control.value } };
        }
      }
      return null;
    };
  }

  isExistValidator(url, message): AsyncValidatorFn {
    return (control: AbstractControl): Promise<ValidationErrors | null> | Observable<ValidationErrors | null> => {
      const api = new ValidatorService();
      const value = control.value;
      const UPLOAD_DIRECTIVE_API_URL = AppInjector.get('UPLOAD_DIRECTIVE_API_URL');
      const UPLOAD_DIRECTIVE_HEADERS = AppInjector.get('UPLOAD_DIRECTIVE_HEADERS');

      return new Promise(async (resolve, reject) => {
        if (value) {
          const result = await api.apiService(UPLOAD_DIRECTIVE_API_URL + url + value, UPLOAD_DIRECTIVE_HEADERS).toPromise();
          if (result.exist === true) {
            resolve({ isExist: { valid: false, value: control.value, message: message } });
          }
          resolve(null);
        }
        resolve(null);
      });
    };
  }
}


export const DefaultValidators = new Validators();
