import { Component, OnInit, Input, forwardRef, ElementRef, AfterViewInit, OnChanges } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import * as _ from 'lodash';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'mutiple-checkbox',
  templateUrl: './mutiple-checkbox.component.html',
  styleUrls: ['./mutiple-checkbox.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => MutipleCheckboxComponent),
      multi: true
    }
  ]
})
export class MutipleCheckboxComponent implements ControlValueAccessor, AfterViewInit, OnChanges {
  @Input()
  options: any[];
  @Input()
  _value: any[];
  @Input()
  isDisabled: Boolean;
  @Input()
  isClear: Boolean;
  public checkedList = [];

  public selected;
  private disabled: boolean;
  private onChange: Function;
  private onTouched: Function;

  constructor(private element: ElementRef) {
    this.onChange = (val: any) => {};
    this.onTouched = () => {};
    this.disabled = false;
  }

  writeValue(val: any): void {
    if (val) {
      this.value = val;
    }
  }

  get value() {
    return this._value;
  }

  set value(val) {
    this.selected = _.find(this.options, item => item.value === val);
    this._value = val;
    this.onChange(this._value);
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  // tslint:disable-next-line:use-life-cycle-interface
  ngOnInit() {}

  ngOnChanges() {
    setTimeout(() => {
      if (this.isClear === true) {
        if (!_.isUndefined(this.options) && !_.isEmpty(this.options)) {
          _.map(this.options, i => {
            i.checked = undefined;
          });
          this.checkedList = [];
          this.value = this.checkedList;
        }
      }
    }, 2);
  }

  ngAfterViewInit() {
    setTimeout(() => {
      if (this.options !== null && this.options !== undefined && !_.isEmpty(this.options)) {
        const $this = this;
        _.forEach(this.options, function(value) {
          if (value.checked === true) {
            $this.checkedList.push(value.value);
          }
        });
        this.value = this.checkedList;
      }
      this.value = this.checkedList;
    }, 1);
  }

  onCheckboxChange(option, event) {
    if (event.target.checked) {
      this.checkedList.push(option.value);
    } else {
      for (let i = 0; i < this.options.length; i++) {
        if (this.checkedList[i] === option.value) {
          this.checkedList.splice(i, 1);
        }
      }
    }

    this.value = this.checkedList;
  }
}
