import { Component, Input, forwardRef, AfterViewInit, OnChanges } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { Countries } from './countries';
import * as _ from 'lodash';
declare const $: any;

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'app-phone-input',
  templateUrl: './phone-input.component.html',
  styleUrls: ['./phone-input.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => PhoneInputComponent),
      multi: true
    }
  ]
})
export class PhoneInputComponent implements ControlValueAccessor, AfterViewInit, OnChanges {
  @Input() isDisabled: Boolean;
  @Input() _value = {
    code: '',
    value: '',
    alpha2Code: ''
  };
  public COUNTRIES = Countries;
  propagateChange = (val: any) => {};

  get value() {
    return this._value;
  }

  set value(val) {
    this._value = val;
    this.propagateChange(this._value);
  }

  ngOnChanges(): void {
    if (this.value) {
      $('#rf-phone-code')
        .select2()
        .val(this.value.code + '-' + this.value.alpha2Code)
        .trigger('change.select2');
    }
  }

  ngAfterViewInit() {
    const $this = this;
    setTimeout(() => {
      $('#rf-phone-code').select2();
      if (this.value) {
        $('#rf-phone-code')
          .select2()
          .val(this.value.code + '-' + this.value.alpha2Code)
          .trigger('change.select2');
      }
      $('#rf-phone-code').change(function() {
        // tslint:disable-next-line:prefer-const
        const val = $(this)
          .val()
          .split('-');
        $this.value = { ...$this.value, code: val[0], alpha2Code: val[1] };
      });
    }, 1000);
  }

  selectCountry(val) {
    val = val.split('-');
    this.value = { ...this.value, code: val[0], alpha2Code: val[1] };
  }

  enterPhone(val) {
    this.value = { ...this.value, value: val };
  }

  writeValue(value: any) {
    if (value !== undefined) {
      this.value = value;
    }
  }

  registerOnChange(fn) {
    this.propagateChange = fn;
  }

  registerOnTouched() {}
}
