import { Component, OnInit, Input, forwardRef, AfterViewInit } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'ranger-silders',
  templateUrl: './ranger-silders.component.html',
  styleUrls: ['./ranger-silders.component.css'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => RangerSildersComponent),
      multi: true
    }
  ]
})
export class RangerSildersComponent implements ControlValueAccessor, AfterViewInit {
  @Input() id;
  @Input() min;
  @Input() miLabel;
  @Input() unit;
  @Input() max;
  @Input() step;
  @Input() _value = {
    min: null,
    max: null
  };
  @Input() oldValue;
  propagateChange = (val: any) => {};

  constructor() {}

  ngAfterViewInit() {
    setTimeout(() => {
      if (this.oldValue) {
        this.value = this.oldValue;
      } else {
        this.value = {
          min: this.min,
          max: this.max
        };
      }

      const $this = this;
      function getVals() {
        // Get slider values
        const parent = this.parentNode;
        const slides = parent.getElementsByTagName('input');
        let slide1 = parseFloat(slides[0].value);
        let slide2 = parseFloat(slides[1].value);
        // Neither slider will clip the other, so make sure we determine which is larger
        if (slide1 > slide2) {
          const tmp = slide2;
          slide2 = slide1;
          slide1 = tmp;
        }
        const miLabel = $this.miLabel ? $this.miLabel + ': ' : '';
        const unit = $this.unit ? $this.unit : '';

        const displayElement = parent.getElementsByClassName('rangeValues')[0];
        displayElement.innerHTML = miLabel + slide1 + unit + ' - ' + slide2 + unit;
        $this.value = {
          min: slide1,
          max: slide2
        };
      }

      window.onload = function() {
        // Initialize Sliders
        const sliderSections = document.getElementsByClassName('range-slider');
        for (let x = 0; x < sliderSections.length; x++) {
          const sliders = sliderSections[x].getElementsByTagName('input');
          for (let y = 0; y < sliders.length; y++) {
            if (sliders[y].type === 'range') {
              sliders[y].oninput = getVals;
              // Manually trigger event first time to display values
              sliders[y].oninput(null);
            }
          }
        }
      };
    }, 1000);
  }

  get value() {
    return this._value;
  }

  set value(val) {
    this._value = val;
    this.propagateChange(this._value);
  }

  writeValue(value: any) {
    if (value !== undefined) {
      this.value = value;
    }
  }

  registerOnChange(fn) {
    this.propagateChange = fn;
  }

  registerOnTouched() {}
}
