import { InputBase } from './InputBase';

export class TypeAheadWithoutApi extends InputBase<string> {
  controlType = 'type-ahead-without-api';
  options: [];
  fieldName: Function;
  id: number;
  searchBy: string;

  constructor(options: {} = {}) {
    super(options);
    this.options = options['options'] || [];
    this.fieldName = options['fieldName'] || [];
    this.id = options['id'] || 1;
    this.searchBy = options['searchBy'] || [];
  }
}
