import * as _ from 'lodash';
import { FileService } from './test';
declare const $: any;
import { Component, OnInit, Input, forwardRef, ElementRef, AfterViewInit, OnChanges, Injector } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'upload-images',
  templateUrl: './upload-images.component.html',
  styleUrls: ['./upload-images.component.css'],
  providers: [
    FileService,
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => UploadImagesComponent),
      multi: true
    }
  ]
})
export class UploadImagesComponent implements OnInit, ControlValueAccessor, AfterViewInit, OnChanges {
  @Input() id: number;
  @Input() headers: string;
  @Input() apiUpload: string;
  @Input() accept: string;
  @Input() subProgress: any;
  @Input() subError: any;
  @Input() allowMaxSize: any;
  @Input() uploadPath: any;
  @Input() multiple: Boolean;
  @Input() _value;
  @Input() resultTransformer;
  @Input() paramName;
  @Input() oldValue;
  @Input() isClear: Boolean;
  @Input() zoom: Boolean;
  inProgress: any = {
    process: false,
    error: false
  };
  files;
  number_file_upload_success = 0;
  response_data;
  valid_extensions;
  fileList = [];
  isload = false;
  isShowDetail = false;
  urlDetail;
  errorMessage;
  imageFormats = ['jpg', 'jpeg', 'png', 'gif', 'tiff', 'bmp'];
  private disabled: boolean;
  private onChange: Function;
  private onTouched: Function;
  constructor(private injector: Injector, private api: FileService, private element: ElementRef) {
    this.onChange = (val: any) => {};
    this.onTouched = () => {};
    this.disabled = false;
  }

  writeValue(val: any): void {
    if (val) {
      this.value = val;
    }
  }

  get value() {
    return this._value;
  }

  set value(val) {
    this._value = val;
    this.onChange(this._value);
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  ngAfterViewInit() {
    setTimeout(() => {
      this.actionRender();
    }, 1);
  }

  ngOnChanges() {
    if (this.isClear === true) {
      setTimeout(() => {
        if (!_.isNil(this.multiple) && this.multiple === true) {
          this.oldValue = [];
          this.fileList = [];
        } else {
          this.oldValue = null;
        }
        const $this = this;
        if (!_.isNil(this.multiple) && this.multiple === true && !_.isEmpty($this.oldValue)) {
          _.forEach(this.oldValue, function(value) {
              $this.fileList.push(value);
              $this.value = $this.fileList;
          });
        } else {
          $this.value = $this.oldValue;
        }
      }, 1);
    }
  }

  actionRender() {
    // tslint:disable-next-line:prefer-const
    let $this = this;
    this.isload = true;
    if (!_.isNil(this.multiple) && this.multiple === true && !_.isEmpty($this.oldValue)) {
      _.forEach(this.oldValue, function(value) {
        $this.fileList.push(value);
        $this.value = $this.fileList;
      });
    } else {
      $this.value = $this.oldValue;
    }
    this.isload = false;

    if (!_.isUndefined(this.accept)) {
      this.valid_extensions = this.accept.split('|');
    }

    $('#uploadfile-reactive-form-' + this.id).click(() => {
      if (this.inProgress[this.subProgress] !== true) {
        // tslint:disable-next-line:prefer-const
        let input = document.createElement('input');
        input.type = 'file';
        if (!_.isNil(this.multiple) && this.multiple === true) {
          input.multiple = true;
        }
        $(input).trigger('click');
        $(input).on('change', event => {
          this.number_file_upload_success = 0;
          this.files = input.files;

          if (this.files.length > 1) {
            this.response_data = [];
            _.forEach(this.files, function(file) {
              $this.validateFile(file);
            });
          }
          if (this.files.length === 1) {
            this.validateFile(this.files[0]);
          }
        });
      }
    });
  }

  ngOnInit() {}

  getFileExtension(file) {
    return file.name.split('.').pop();
  }

  validateFile(file) {
    if (file !== undefined) {
      if (!_.isNil(this.allowMaxSize) && this.allowMaxSize !== '') {
        if (file.size > parseFloat(this.allowMaxSize) * 1024 * 1024) {
          this.errorMessage = `Please select file less than ${this.allowMaxSize}MB`;
          return false;
        }
      }
      // tslint:disable-next-line:max-line-length
      if (
        !_.isUndefined(this.accept) &&
        _.isUndefined(_.find(this.valid_extensions, item => item.toLowerCase() === this.getFileExtension(file).toLowerCase()))
      ) {
        this.errorMessage = 'Please select a file with valid type';
        return false;
      }
      this.upload(file);
    }
  }

  upload(file) {
    const UPLOAD_DIRECTIVE_HEADERS = this.injector.get('UPLOAD_DIRECTIVE_HEADERS');
    const UPLOAD_DIRECTIVE_API_URL = this.injector.get('UPLOAD_DIRECTIVE_API_URL');

    this.isload = true;
    // tslint:disable-next-line:prefer-const
    let $this = this;
    this.inProgress[this.subProgress] = true;
    this.inProgress[this.subError] = false;

    // tslint:disable-next-line:prefer-const
    let params = {
      // files: file
      upload_path: this.uploadPath
    };
    params[this.paramName] = file;
    const requestHeaders = UPLOAD_DIRECTIVE_HEADERS;
    const requestApiUrl = UPLOAD_DIRECTIVE_API_URL + this.apiUpload;
    this.api.upload(params, requestApiUrl, requestHeaders).subscribe(
      response => {
        this.inProgress[this.subProgress] = false;
        this.number_file_upload_success++;
        if (!_.isNil($this.multiple) && $this.multiple === true) {
          if (!$this.zoom) {
            $this.fileList.push($this.resultTransformer(response));
          } else {
            $this.fileList.push({ url: $this.resultTransformer(response), urlZoom: $this.resultTransformer(response) });
          }
          $this.value = $this.fileList;
        } else {
          if (!$this.zoom) {
            $this.value = $this.resultTransformer(response);
          } else {
            $this.value = { url: $this.resultTransformer(response), urlZoom: $this.resultTransformer(response) };
          }
        }
        this.isload = false;
        this.errorMessage = null;
      },
      error => {
        this.isload = false;
        this.inProgress[this.subProgress] = false;
        this.inProgress[this.subError] = true;
        console.log('error', error);
      }
    );
  }

  deleteItem(item) {
    this.fileList.splice(this.fileList.indexOf(item), 1);
    this.value = this.fileList;
  }

  showDetail(val) {
    this.isShowDetail = true;
    this.urlDetail = val;
  }

  closeDetail() {
    this.isShowDetail = false;
  }

  isInclude(value, arr) {
    const accepts = value.split('|');
    let result = true;
    _.forEach(accepts, function(i) {
      if (!_.find(arr, item => item === i)) {
        result = false;
      }
    });
    return result;
  }
}
