import * as _ from 'lodash';
import { FileService } from './test';
declare const $: any;
import { Component, OnInit, Input, forwardRef, ElementRef, AfterViewInit, OnChanges, Injector } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { HttpClient } from '@angular/common/http';
import { NgProgress, NgProgressRef } from '@ngx-progressbar/core';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'upload-file',
  templateUrl: './upload-images.component.html',
  styleUrls: ['./upload-images.component.css'],
  providers: [
    FileService,
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => UploadFilesComponent),
      multi: true
    }
  ]
})
export class UploadFilesComponent implements OnInit, ControlValueAccessor, AfterViewInit, OnChanges {
  @Input() id: number;
  @Input() headers: string;
  @Input() apiUpload: string;
  @Input() accept: string;
  @Input() allowMaxSize: any;
  @Input() uploadPath: any;
  @Input() multiple: Boolean;
  @Input() _value;
  @Input() resultTransformer;
  @Input() paramName;
  @Input() oldValue;
  @Input() isClear: Boolean;
  @Input() title: string;
  @Input() dataFunction: Function;
  @Input() uploadFromDB: boolean;
  @Input() width: string;
  @Input() maxWidth: string;
  @Input() height: string;
  @Input() maxHeight: string;

  inProgress: any = {
    process: false,
    error: false
  };
  files;
  number_file_upload_success = 0;
  response_data;
  valid_extensions;
  fileList = [];
  isload = false;
  isShowDetail = false;
  errorMessage;
  warning;
  imageFormats = ['jpg', 'jpeg', 'png', 'gif', 'djvu', 'raw', 'ico', 'svg', 'tiff', 'bmp'];
  documentFormats = ['pdf', 'doc', 'docx', 'xls', 'xlsx', 'txt', 'rtf', 'odt', 'ppt', 'pptx', 'csv', 'xml'];
  videoFormats = ['3gp', 'asf', 'avi', 'mov', 'mp4', 'rm', 'vob', 'wmv'];
  soundFormats = ['mp3', 'wma', 'wav', 'flac', 'aac', 'ogg', 'aiff', 'alac'];
  private disabled: boolean;
  private onChange: Function;
  private onTouched: Function;

  preventAbuse = false;
  progressRef: NgProgressRef;
  constructor(
    private injector: Injector,
    private api: FileService,
    private element: ElementRef,
    private http: HttpClient,
    private progress: NgProgress
  ) {
    this.onChange = (val: any) => { };
    this.onTouched = () => { };
    this.disabled = false;
  }

  writeValue(val: any): void {
    if (val) {
      this.value = val;
    }
  }

  get value() {
    return this._value;
  }

  set value(val) {
    this._value = val;
    this.onChange(this._value);
  }
  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }

  ngAfterViewInit() {
    setTimeout(() => {
      this.progressRef = this.progress.ref('progress-bar-' + this.id);
      this.actionRender();
    }, 1);
  }

  uploadFile(id) {
    const $this = this;
    if (this.inProgress['process'] !== true) {
      const input = document.createElement('input');
      input.type = 'file';
      if (!_.isNil(this.multiple) && this.multiple === true) {
        input.multiple = true;
      }
      $(input).trigger('click');
      $(input).on('change', event => {
        this.number_file_upload_success = 0;
        this.files = input.files;
        if (this.files.length > 1) {
          this.response_data = [];
          _.forEach(this.files, function (file) {
            $this.validateFile(file);
          });
        }
        if (this.files.length === 1) {
          this.validateFile(this.files[0]);
        }
      });
    }
  }

  ngOnChanges() {
    if (this.isClear === true) {
      setTimeout(() => {
        if (!_.isNil(this.multiple) && this.multiple === true) {
          this.oldValue = [];
          this.fileList = [];
        } else {
          this.oldValue = null;
        }
        const $this = this;
        if (!_.isNil(this.multiple) && this.multiple === true && !_.isEmpty($this.oldValue)) {
          _.forEach(this.oldValue, function (value) {
            $this.fileList.push(value);
            $this.value = $this.fileList;
          });
        } else {
          $this.value = $this.oldValue;
        }
      }, 1);
    }
  }

  actionRender() {
    // tslint:disable-next-line:prefer-const
    let $this = this;
    this.isload = true;
    if (!_.isNil(this.multiple) && this.multiple === true && !_.isEmpty($this.oldValue)) {
      _.forEach(this.oldValue, function (value) {
        $this.fileList.push(value);
        $this.value = $this.fileList;
      });
    } else {
      $this.value = $this.oldValue;
    }
    this.isload = false;

    if (!_.isUndefined(this.accept)) {
      this.valid_extensions = this.accept.split('|');
    }

    // $('#uploadfile_v2-reactive-form-' + this.id).click(() => {
    //   console.log(this.inProgress);
    //   if (this.inProgress['process'] !== true) {
    //     const input = document.createElement('input');
    //     input.type = 'file';
    //     if (!_.isNil(this.multiple) && this.multiple === true) {
    //       input.multiple = true;
    //     }
    //     $(input).trigger('click');
    //     $(input).on('change', event => {
    //       this.number_file_upload_success = 0;
    //       this.files = input.files;
    //       if (this.files.length > 1) {
    //         this.response_data = [];
    //         _.forEach(this.files, function(file) {
    //           $this.validateFile(file);
    //         });
    //       }
    //       if (this.files.length === 1) {
    //         this.validateFile(this.files[0]);
    //       }
    //     });
    //   }
    // });
  }

  ngOnInit() {
    if (!_.isNil(this.maxWidth)) {
      this.warning = `*The image width should be smaller ${this.maxWidth}`;
    }
    if (!_.isNil(this.maxHeight)) {
      this.warning = `*The image width should be smaller ${this.maxHeight}`;
    }
    if (!_.isNil(this.width) && !_.isNil(this.height)) {
      this.warning = `*Recommended size: ${this.width}x${this.height}`;
    } else {
      if (!_.isNil(this.width)) {
        this.warning = `*Recommended width: ${this.width}`;
      }
      if (!_.isNil(this.height)) {
        this.warning = `*Recommended height: ${this.height}`;
      }
    }
  }

  getFileExtension(file) {
    return file.name.split('.').pop();
  }

  validateFile(file) {
    if (file !== undefined) {
      if (!_.isNil(this.allowMaxSize) && this.allowMaxSize !== '') {
        if (file.size > parseFloat(this.allowMaxSize) * 1024 * 1024) {
          this.errorMessage = `Please select file less than ${this.allowMaxSize}MB`;
          return false;
        }
      }
      // tslint:disable-next-line:max-line-length
      if (
        !_.isUndefined(this.accept) &&
        _.isUndefined(_.find(this.valid_extensions, item => item.toLowerCase() === this.getFileExtension(file).toLowerCase()))
      ) {
        this.errorMessage = 'Please select a file with valid type';
        return false;
      }
      this.upload(file);
    }
  }

  upload(file) {
    this.preventAbuse = true;
    this.progressRef.start();
    const UPLOAD_DIRECTIVE_HEADERS = this.injector.get('UPLOAD_DIRECTIVE_HEADERS');
    const UPLOAD_DIRECTIVE_API_URL = this.injector.get('UPLOAD_DIRECTIVE_API_URL');

    this.isload = true;
    // tslint:disable-next-line:prefer-const
    let $this = this;
    this.inProgress['process'] = true;

    // tslint:disable-next-line:prefer-const
    let params = {
      // files: file
      upload_path: this.uploadPath
    };
    params[this.paramName] = file;
    const requestHeaders = UPLOAD_DIRECTIVE_HEADERS;
    const requestApiUrl = UPLOAD_DIRECTIVE_API_URL + this.apiUpload;
    this.api.upload(params, requestApiUrl, requestHeaders).subscribe(
      response => {
        // setTimeout(() => {
        this.progressRef.complete();
        this.inProgress['process'] = false;
        this.number_file_upload_success++;
        if (!_.isNil($this.multiple) && $this.multiple === true) {
          $this.fileList.push($this.resultTransformer(response));
          $this.value = $this.fileList;
        } else {
          $this.value = $this.resultTransformer(response);
          if (response) {
            if (!_.isNil($this.width) || !_.isNil($this.height) || !_.isNil($this.maxWidth) || !_.isNil($this.maxHeight)) {
              $this.getImageDimension(response.data[0].full_path);
            }
          }
        }
        this.isload = false;
        this.errorMessage = null;
        this.preventAbuse = false;
        // }, 800);
      },
      error => {
        this.isload = false;
        this.inProgress['process'] = false;
        console.log('error', error);
        this.errorMessage = error.message;
        this.progressRef.complete();
      }
    );
  }

  getImageDimension = (imgUrl) => {
    let check = true;
    const img = new Image();
    img.src = imgUrl;
    img.onload = (event) => {
      const loadedImage: any = event.currentTarget;
      const width = loadedImage.width;
      const height = loadedImage.height;
      if (!_.isNil(this.width)) {
        if (width !== this.width) {
          check = false;
          this.errorMessage = `*Image width should be equal to ${this.width}px`;
        }
      }
      if (!_.isNil(this.maxWidth)) {
        if (width > this.maxWidth) {
          check = false;
          this.errorMessage = `*Image width should be smaller ${this.maxWidth}px`;
        }
      }
      if (!_.isNil(this.height)) {
        if (height !== this.height) {
          check = false;
          this.errorMessage = `*Image height should be equal to ${this.height}px`;
        }
      }
      if (!_.isNil(this.maxHeight)) {
        if (height > this.maxHeight) {
          check = false;
          this.errorMessage = `*Image height should be smaller ${this.maxHeight}px`;
        }
      }
      if (!check) {
        this.warning = null;
      }
    };
  }

  deleteItem(item) {
    if (this.multiple === true) {
      this.fileList.splice(this.fileList.indexOf(item), 1);
      this.value = this.fileList;
    } else {
      this.value = null;
    }
  }

  isInclude(value, arr) {
    const accepts = value.split('|');
    let result = true;
    _.forEach(accepts, function (i) {
      if (!_.find(arr, item => item === i)) {
        result = false;
      }
    });
    return result;
  }

  getIcon(accepts) {
    const $this = this;
    accepts = accepts.split('|');
    let result = 'fa-exclamation-triangle';
    _.forEach(accepts, function (i) {
      if (_.find($this.documentFormats, item => item === i)) {
        result = 'fa-file-text-o';
      }
      if (_.find($this.videoFormats, item => item === i)) {
        result = 'fa-file-video-o';
      }
      if (_.find($this.soundFormats, item => item === i)) {
        result = 'fa-file-audio-o';
      }
    });
    return result;
  }

  okok() {
    this.dataFunction();
  }
}
