import { InputBase } from './InputBase';

export class TextBox extends InputBase<string> {
  controlType = 'textbox';
  type: string;
  min: number;
  max: number;
  placeholder: string;

  constructor(options: {} = {}) {
    super(options);
    this.type = options['type'] || '';
    this.min = options['min'];
    this.max = options['max'];
    this.placeholder = options['placeholder'] || '';
  }
}
