import { InputBase } from './InputBase';

export class TextBoxMask extends InputBase<string> {
  controlType = 'textbox-mask';
  mask: {}[] = [];
  valueWithCharacter: boolean;
  guide: boolean;
  placeholder: string;
  // example Date:  [/\d/, /\d/, '/', /\d/, /\d/, '/', /\d/, /\d/, /\d/, /\d/]
  constructor(options: {} = {}) {
    super(options);
    this.mask = options['mask'] || [];
    this.valueWithCharacter = options['valueWithCharacter'] || false;
    this.guide = options['guide'] || false;
    this.placeholder = options['placeholder'] || '';
  }
}
