import { Component, OnInit, forwardRef, Input, AfterViewInit } from '@angular/core';
import { NG_VALUE_ACCESSOR, ControlValueAccessor } from '@angular/forms';

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'captcha',
  templateUrl: './captcha.component.html',
  styleUrls: ['./captcha.component.css'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => CaptchaComponent),
      multi: true
    }
  ]
})
export class CaptchaComponent implements ControlValueAccessor, OnInit, AfterViewInit {
  @Input() _captchaData = {
    value: '',
    code: ''
  };
  @Input() numberOfChar: Number;
  @Input() type: String;
  @Input() captchaCorrect: Boolean;
  @Input() message: String;

  constructor() {}

  ngOnInit() {}
  ngAfterViewInit() {
    setTimeout(() => {
      this.captchaData = { ...this.captchaData, code: this.random() };
    }, 1);
  }
  propagateChange = (val: any) => {};

  get captchaData() {
    return this._captchaData;
  }

  set captchaData(val) {
    this._captchaData = val;
    this.propagateChange(this._captchaData);
  }

  writeValue(value: any) {
    if (value !== undefined) {
      this.captchaData = value;
    }
  }

  registerOnChange(fn) {
    this.propagateChange = fn;
  }

  registerOnTouched() {}

  randIntWithZero() {
    return Math.floor(Math.random() * 10);
  }

  random() {
    let code = '';
    let characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    if (this.type === 'number') {
      characters = '0123456789';
      // for (let i = 0; i < this.numberOfChar; i++) {
      //   code += this.randIntWithZero();
      // }
    } else if (this.type === 'alphabet') {
      characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz';
    }
    const charactersLength = characters.length;
    for (let i = 0; i < this.numberOfChar; i++) {
      code += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return code;
  }

  randomCode() {
    this.captchaData = { ...this.captchaData, code: this.random() };
  }
}
