import { InputBase } from './InputBase';

export class DropZone extends InputBase<string> {
  controlType = 'dropzone';
  message: string;
  upload_path: string;
  acceptedFiles: string;
  maxFilesize: number;
  maxFiles: number;
  showPreview: boolean;
  url: string;
  headers: object;
  paramName: string;
  resultTransformer: Function;

  constructor(options: {} = {}) {
    super(options);
    this.message = options['message'] || '';
    this.upload_path = options['upload_path'] || '';
    this.acceptedFiles = options['acceptedFiles'] || '*';
    this.maxFilesize = options['maxFilesize'] || 50;
    this.maxFiles = options['maxFiles'] || 0;
    this.showPreview = options['showPreview'] || false;
    this.url = options['url'] || '';
    this.headers = options['headers'] || null;
    this.paramName = options['paramName'] || 'file';
    this.resultTransformer = options['resultTransformer'];
  }
}
