import {
  Component,
  OnInit,
  Input,
  forwardRef,
  AfterViewInit,
  Inject,
  OnChanges,
  Injector,
  ViewChild,
  ElementRef,
  HostListener
} from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { SelectionService } from './selection-by-api';
import * as _ from 'lodash';
declare const $: any;

@Component({
  // tslint:disable-next-line:component-selector
  selector: 'selection-by-api',
  templateUrl: './selection-by-api.component.html',
  styleUrls: ['./selection-by-api.component.css'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => SelectionByApiComponent),
      multi: true
    },
    SelectionService
  ]
})
export class SelectionByApiComponent implements ControlValueAccessor, OnChanges, AfterViewInit {
  @Input() _value;
  @Input() resultTransformer;
  @Input() apiUpload;
  @Input() headers;
  @Input() fieldName;
  @Input() lengthToSearch;
  @Input() isClear: Boolean;
  @Input() method;
  @Input() id;
  public text;
  public options;
  public isShow = false;
  public isSearching = false;
  public UPLOAD_DIRECTIVE_HEADERS = this.injector.get('UPLOAD_DIRECTIVE_HEADERS');
  public UPLOAD_DIRECTIVE_API_URL = this.injector.get('UPLOAD_DIRECTIVE_API_URL');
  public KEY_CODE = {
    UP_ARROW: 38,
    DOWN_ARROW: 40
  };
  public url = (apiUrl, search) => {
    return apiUrl + this.apiUpload + search;
    // tslint:disable-next-line:semicolon
  };
  constructor(private api: SelectionService, private injector: Injector) {}
  propagateChange = (val: any) => {};

  ngAfterViewInit() {
    const $this = this;
    setTimeout(() => {
      $('body').click(function() {
        if (event.target !== document.getElementById('search-single-selection-reative-form-' + this.id)) {
          $this.isShow = false;
        }
      });
      if (this.value) {
        this.text = this.fieldName(this.value);
        const api = this.url(this.UPLOAD_DIRECTIVE_API_URL, this.fieldName(this.value));
        const requestHeaders = this.UPLOAD_DIRECTIVE_HEADERS;
        this.api.getAll(api, requestHeaders, this.method).subscribe(response => {
          this.options = this.resultTransformer(response);
          this.addSelected();
        });
      }
    }, 1);
  }

  ngOnChanges() {
    if (this.isClear === true) {
      setTimeout(() => {
        this.value = null;
        this.text = null;
      }, 1);
    }
  }

  filterData(event): void {
    document.getElementById('search-single-selection-reative-form-' + this.id).setAttribute('disabled', 'true');
    this.options = [];
    if (event.length <= 0) {
      this.value = null;
      this.options = [];
      document.getElementById('search-single-selection-reative-form-' + this.id).removeAttribute('disabled');
      document.getElementById('search-single-selection-reative-form-' + this.id).focus();
    }
    if (event.length >= this.lengthToSearch) {
      this.isSearching = true;
      this.isShow = true;
      this.value = null;
      const api = this.url(this.UPLOAD_DIRECTIVE_API_URL, event);
      const requestHeaders = this.UPLOAD_DIRECTIVE_HEADERS;
      this.api.getAll(api, requestHeaders, this.method).subscribe(response => {
        this.options = this.resultTransformer(response);
        this.isSearching = false;
        document.getElementById('search-single-selection-reative-form-' + this.id).removeAttribute('disabled');
        document.getElementById('search-single-selection-reative-form-' + this.id).focus();
      });
    }
  }

  addSelected() {
    const $this = this;
    _.map(this.options, (item, key) => {
      delete item.selected;
      if (_.isEqual(item, $this.value)) {
        _.assign(item, { selected: true });
      }
    });
  }

  detailClick(item) {
    this.setValue(item);
    this.isShow = false;
  }

  changeStatusOptions() {
    this.isShow === false ? (this.isShow = true) : (this.isShow = false);
  }

  onKeydown(event) {
    if (event.keyCode === this.KEY_CODE.DOWN_ARROW && this.isShow) {
      _.forEach(this.options, i => delete i.selected);
      if (this.value) {
        const current_item = _.find(this.options, i => this.fieldName(i) === this.fieldName(this.value));
        if (current_item) {
          const index_current_item = this.options.indexOf(current_item);
          const last_item = this.options.slice(-1).pop();
          const index_last_item = this.options.indexOf(last_item);
          if (index_current_item === index_last_item) {
            this.value = this.options[0];
          } else {
            this.value = this.options[index_current_item + 1];
          }
        } else {
          this.value = this.options[0];
        }
      } else {
        this.value = this.options[0];
      }
      this.setValue(this.value);
    }
  }

  onKeyup(event) {
    if (event.keyCode === this.KEY_CODE.UP_ARROW && this.isShow) {
      _.forEach(this.options, i => delete i.selected);
      if (this.value) {
        const current_item = _.find(this.options, i => this.fieldName(i) === this.fieldName(this.value));
        if (current_item) {
          const index_current_item = this.options.indexOf(current_item);
          const head_item = this.options[0];
          const index_head_item = this.options.indexOf(head_item);
          if (index_current_item === index_head_item) {
            this.value = this.options.slice(-1).pop();
          } else {
            this.value = this.options[index_current_item - 1];
          }
        } else {
          this.value = this.options.slice(-1).pop();
        }
      } else {
        this.value = this.options.slice(-1).pop();
      }
      this.setValue(this.value);
    }
  }

  _isEmpty(array) {
    return _.isEmpty(array) ? true : false;
  }

  setValue(item) {
    this.text = this.fieldName(item);
    this.value = item;
    this.addSelected();
  }

  get value() {
    return this._value;
  }

  set value(val) {
    this._value = val;
    this.propagateChange(this._value);
  }

  writeValue(value: any) {
    if (value !== undefined) {
      this.value = value;
    }
  }

  registerOnChange(fn) {
    this.propagateChange = fn;
  }

  registerOnTouched() {}
}
